.Flexbox {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
  flex-direction: row;
  -webkit-justify-content: flex-start;
  justify-content: flex-start;
  -webkit-align-items: stretch;
  align-items: stretch;
  background-color: rgba(255, 255, 255, 0.05);
}

/* HACK: */
.Flexbox > .VerticalBox {
  width: calc(100% / 6);
}
